import {NgModule} from '@angular/core';
import {PreloadAllModules, RouterModule, Routes} from '@angular/router';
import {HomeComponent} from './core/home/home.component';
import {LoginComponent} from './auth/login/login.component';
import {NotfoundComponent} from './core/notfound/notfound.component';
import {SignupComponent} from './auth/signup/signup.component';
import {ForgotpasswordComponent} from './auth/forgotpassword/forgotpassword.component';
import {ProfileviewComponent} from './core/profile/profileview/profileview.component';
import {ProfileeditComponent} from './core/profile/profileedit/profileedit.component';
import {ProfileComponent} from './core/profile/profile.component';
import {MobileverifyComponent} from './auth/mobileverify/mobileverify.component';
import {OrdersComponent} from './core/orders/orders.component';

const appRoutes: Routes = [
  {path: '', redirectTo: '/home', pathMatch: 'full'},
  {path: 'home', component: HomeComponent},
  {
    path: 'login', component: LoginComponent
  },
  {
    path: 'register', component: SignupComponent
  },
  {
    path: 'forgotpassword', component: ForgotpasswordComponent
  },
  {
    path: 'mobileVerify', component: MobileverifyComponent
  },
  {
    path: 'profile', component: ProfileComponent, children: [
      {path: '', redirectTo: 'editProfile', pathMatch: 'full'},
      {path: 'viewProfile', component: ProfileviewComponent},
      {path: 'editProfile', component: ProfileeditComponent},
      {path: 'orders', component: OrdersComponent}
    ]

  },
  {
    path: 'roles', loadChildren: () => import('./roles/role.module').then(module => module.RoleModule)
  },
  {
    path: 'd', loadChildren: () => import('./demand/demand.module').then(module => module.DemandModule)
  },
  {path: '**', component: NotfoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes, {preloadingStrategy: PreloadAllModules})],
  exports: [RouterModule]
})
export class AppRoutingModule {

}
