import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs';
import {Store} from '@ngrx/store';
import {AppState} from '../../store/app.reducer';
import {AuthService} from '../auth.service';
import {NgForm} from '@angular/forms';
import * as AuthActions from '../store/auth.actions';
import {ActivatedRoute, ParamMap} from '@angular/router';
import {ProfileModel} from '../../core/model/profile.model';

@Component({
  selector: 'app-mobileverify',
  templateUrl: './mobileverify.component.html',
  styleUrls: ['./mobileverify.component.css']
})
export class MobileverifyComponent implements OnInit, OnDestroy {

  mobileCheck: Subscription;
  mobileCheckToken = false;
  otpCheck: Subscription;
  otpCheckToken = false;


  isMobileVerified = false;
  isOtpVerified = false;

  mobile: string;
  name: string;
  email: string;

  profileData = new ProfileModel();
  role = {
    dob: {
      year: null,
      month: null,
      day: null
    }
    ,
    address: {
      permanentAddress: {
        pFullAddress: '',
        pPostalCode: null
      },
      currentAddress: {
        cFullAddress: '',
        cPostalCode: null
      }
    },
    roles: {
      business: {
        realEstateBusiness: false,
        transportationBusiness: false,
        travelCuratorBusiness: false,
        serviceSupplierBusiness: false,
        financeBusiness: false,
        propertiesBusiness: false,
        travelAgentBusiness: false
      },
      activityExperts: {
        wellnessExperts: false,
        waterExperts: false,
        mountainAdventureExperts: false,
        autoAdventureExperts: false,
        airAdventureExperts: false,
        otherExperts: false
      },
      jobBoard: {
        hotelStaffJob: false,
        wellnessMeditationJob: false,
        sportsGuideJob: false,
        hobbyExpertJob: false
      }
    }
  };


  constructor(private store: Store<AppState>, private authService: AuthService, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.route.queryParams.subscribe(
      (params: ParamMap) => {
        if (params[`auth_key`]) {
          this.store.dispatch(new AuthActions.SetToken(params[`auth_key`]));
          this.store.dispatch(new AuthActions.TryAuthSignIn());
        } else {
          this.profileData.name = params[`name`];
          this.profileData.email = params[`email`];
          Object.assign(this.profileData, this.role);
          // this.profileData.roles = this.role.roles;
        }
        console.log(this.profileData);
      }
    );


    this.mobileCheck = this.authService.loginMobileCheckToken.subscribe(
      (value) => {
        if (value === true) {
          this.isMobileVerified = true;
        } else {
          this.mobileCheckToken = true;
        }
      }
    );

    this.otpCheck = this.authService.loginOtpCheckToken.subscribe(
      (value) => {
        if (value === true) {
          this.signUp();
        } else {
          this.otpCheckToken = true;
        }
      }
    );
  }

  verifymobile(form: NgForm) {
    this.mobile = form.value.mobile;
    this.store.dispatch(new AuthActions.SendOtp({type: 'socialMedia', mobile: this.mobile}));
  }

  verifyotp(form: NgForm) {
    const otp = (+form.value.otp1) + '' + (+form.value.otp2) + '' + (+form.value.otp3) + '' + (+form.value.otp4);
    this.store.dispatch(new AuthActions.VerifyOtp({mobile: this.mobile, otp: +otp}));
  }

  private signUp() {
    Object.assign(this.profileData, {mobile: this.mobile, password: 'uspl1234@'});
    this.store.dispatch(new AuthActions.TrySignUp(this.profileData));
  }

  keytab(event) {
    const element = event.srcElement.nextElementSibling; // get the sibling element
    if (element == null) {
      return;
    } else {
      element.focus();
    }
  }

  ngOnDestroy(): void {
    this.otpCheck.unsubscribe();
    this.mobileCheck.unsubscribe();
  }
}
