import {Component, OnInit} from '@angular/core';
import {PipeTransform} from '@angular/core';
import {DecimalPipe} from '@angular/common';
import {FormControl} from '@angular/forms';

import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {OrderModel} from './order.model';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../shared/serverEndpoints';
import {ToastService} from '../../shared/toast/toast.service';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {
  initialOrders: OrderModel[];
  orders: Observable<OrderModel[]>;
  filter = new FormControl('');

  constructor(pipe: DecimalPipe, private http: HttpClient, private toastService: ToastService) {
    this.orders = this.filter.valueChanges.pipe(
      startWith(''),
      map(text => this.search(text, pipe))
    );
  }

  ngOnInit(): void {
    this.http.get(endPoints.getBookings, {observe: 'response'}).subscribe(
      (response: HttpResponse<any>) => {
        if (response.status === 200) {
          this.initialOrders = [];
          response.body.forEach(order => {
            this.initialOrders.push(order);
          });
          console.log(this.initialOrders);
        } else {
          this.toastService.showDefault('No Orders found! Thanks');
        }
      }
    );
  }

  //
  //
  search(text: string, pipe: PipeTransform): OrderModel[] {
    return this.initialOrders.filter(order => {
      const term = text.toLowerCase();
      return order.type.toLowerCase().includes(term)
        || pipe.transform(order.transaction.amount).includes(term);
      // || pipe.transform(country.population).includes(term);
    });
  }


}
